<template>
  <Chart-org :data="data"></Chart-org>
</template>
<script>
import ChartOrg from './components/Chart-org.vue';
import * as d3 from 'd3';

export default {
  data() {
    return {
      data: null
    };
  },
  components: {
    ChartOrg
  },
  created() {
    d3.csv(
      'https://raw.githubusercontent.com/bumbeishvili/sample-data/main/org.csv'
    ).then(d => {
      console.log('fetched data');
      this.data = d;
    });
  }
}
</script>
